<template>
  <div>
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>我的麦粒</span>
    </header>
    <p class="inter"></p>
    <div class="kernel">
      <p>我的麦粒个数</p>
      <h2>{{num}}</h2>
      <img src="../../assets/person/kernel.png" alt="">
    </div>
    <p class="inter"></p>
    <div class="kernel_record">
      <section>
        <img src="../../assets/person/round_l.png" alt="">
        <span class="record_name">麦粒明细</span>
        <img src="../../assets/person/round_r.png" alt="">
      </section>
      <ul v-infinite-scroll="loadMore" infinite-scroll-disabled="moreLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="false">
        <li class="record_list" v-for="item in list">
          <section>
            <p>{{item.type | typeFilter}}</p>
          </section>
          <span>
            <span v-if="item.type === 0">+</span>
            <span v-else>-</span>
            {{item.number}}
          </span>
        </li>
        <li class="more_loading" v-show="!queryLoading">
          <mt-spinner type="fading-circle"  :size="25" v-show="moreLoading&&!allLoaded"></mt-spinner>
          <span v-show="allLoaded">已全部加载</span>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  export  default {
    data (){
      return {
        list: [],
        num: 0,
        moreLoading: false,
        queryLoading: false,
        allLoaded: false,
        pageSize: 20,
        pageNum: 1,
        totalNum: 0,
      }
    },
    filters:{
      typeFilter(val){
        if (val === 0){
          return '开店获得'
        } else if (val === 1) {
          return '混合商品兑换'
        } else {
          return '积分商品'
        }
      }
    },
    methods:{
      back(){
        this.$router.go(-1);
      },
      loadMore(){
        if(this.allLoaded){
          this.moreLoading = true;
          return;
        }
        if(this.queryLoading){
          return;
        }
        this.moreLoading = !this.queryLoading;
        this.pageNum++;
        this.getRecord(this.pageNum);
      },
      getNum(){
        this.post('home/User/getUserInfo', {}).then(res => {
          if (res.data.code === 10000) {
              this.num = res.data.info.jifen;
          }
        })
      },
      getRecord(page = 1){
        this.post('home/User/getJiFenLog', {
          page: page,
          num: 20
        }).then(res => {
          if (res.data.code === 10000) {
            this.totalNum = res.data.info.number;
            this.list = this.list.concat(res.data.info.data);
            this.allLoaded = this.list.length === this.totalNum;
            this.moreLoading = this.allLoaded;
          }
        })
      }
    },
    mounted(){
      this.getNum();
      this.getRecord();
    }
  }
</script>
<style scoped>
@import "../../../static/header.css";
.inter {
  height: 0.2rem;
  background-color: #F2F2F2;
}
.kernel {
  padding: 0.6rem 0.4rem;
  position: relative;
  height: 2.16rem;
  overflow: hidden;
}
  .kernel p {
    font-size: 0.24rem;
    color: #666666;
    margin-bottom: 0.15rem;
  }
  .kernel h2 {
    color: #ff6f00;
    font-size: 0.58rem;
  }
  .kernel img {
    width: 1.86rem;
    position: absolute;
    right: -0.2rem;
    bottom: -0.1rem;
  }
  .kernel_record {
    padding: 0.3rem 0.4rem;
    text-align: center;
  }
  .kernel_record img {
    width: 1.02rem;
    margin: 0 0.1rem;
  }
  .record_name {
    font-size: 0.3rem;
    color: #cf9f82;
  }
  .record_list {
    height: 1.24rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #F2F2F2;
  }
  .record_list p {
    text-align: left;
  }
.record_list p:first-child {
  font-size: 0.3rem;
  color: #111111;
}
.record_list p:last-child {
  font-size: 0.28rem;
  color: #666666;
}
.record_list span {
  font-size: 0.34rem;
  color: #ff6f00;
}
</style>



